.course__header {
  height: 46vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.course__header .course__background {
  z-index: 0;
  height: inherit;
  width: 100%;
  object-fit: cover;
  margin: auto;
  display: block;
  position: absolute;
  inset: 0;
}

.course__header .course__information {
  width: 1200px;
  margin: 0 auto;
}

.course__header .course__information .course__title {
  z-index: 1;
  text-align: center;
  color: #2c2c2c;
  color: #fff;
  margin-bottom: 30px;
  font-size: 72px;
  font-weight: 700;
  line-height: 83px;
  position: relative;
}

.course__header .course__information .course__cipher {
  z-index: 1;
  text-align: center;
  color: #2c2c2c;
  color: #fff;
  font-size: 46px;
  font-weight: 400;
  line-height: 53px;
  position: relative;
}

.profiles__container {
  background: url("../courses_background.5a997d45.svg") bottom / contain no-repeat;
  padding: 150px 0;
}

@media (max-width: 1170px) {
  .profiles__container {
    background-size: cover;
  }
}

.profiles__container .profiles__title {
  text-align: center;
  color: #2c2c2c;
  margin-bottom: 50px;
  font-size: 72px;
  font-weight: 700;
  line-height: 83px;
}

.card__parent .card__content {
  grid-gap: 50px;
}

.card__parent .card__container {
  cursor: grab;
}

.card__subject .card__topic {
  color: #2c2c2c;
  text-align: center;
  margin: 100px 0 50px;
  font-size: 46px;
  font-weight: 700;
  line-height: 53px;
}

.card__subject .card__special {
  margin: 75px 0 50px;
}

.card__subject .subject {
  grid-gap: 25px 9px;
  grid-template-columns: repeat(8, 1fr);
  justify-content: center;
  align-items: flex-start;
  display: grid;
}

.card__subject .subject .subject__container {
  width: 120px;
}

.card__subject .subject .subject__container .subject__icon {
  width: 100px;
  height: 100px;
  background: radial-gradient(73.81% 92.33% at 8.65% 12.15%, #65bfff 0%, #ff6fce 100%);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 0 auto 10px;
  display: flex;
}

.card__subject .subject .subject__container .subject__icon img {
  display: block;
}

.card__subject .subject .subject__container .subject__name {
  text-align: center;
  color: #2c2c2c;
  word-wrap: break-word;
  font-size: 12px;
  line-height: 14px;
}

.card__subject .subject .subject__container__disabled .subject__icon {
  background: #cbcbcb;
}

.card__subject .subject .subject__container__disabled .subject__name__disabled {
  color: #2c2c2c80;
}

